* {
    box-sizing: border-box;
}
html {
    font-family: 'Graphik LCG';
    font-weight: 400;
    font-style: normal;
    font-size: 14px;
    color: #5e646a;
}
html,
body {
    padding: 0;
    margin: 0;
}
html,
body,
#root {
    height: 100%;
}
:root {
    --title-color: rgb(25, 28, 31);
}
ul {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
.container {
    max-width: 1246px;
    padding-left: 15px;
    padding-right: 15px;
    height: 100%;
    margin: 0 auto;
}
h1,
h2,
h3 {
    margin: 0;
    padding: 0;
}
img {
    max-width: 100%;
}
.swiper-initialized {
    padding-bottom: 70px !important;
    @media ($tablet) {
        padding-bottom: 50px !important;
    }
    @media ($phone) {
        padding-bottom: 40px !important;
    }
}
.title {
    text-align: center;
    line-height: 56px;
    font-size: 40px;
    color: var(--title-color);
    font-weight: 600;
    @media ($phone) {
        font-size: 24px;
        line-height: 32px;
    }
}
